import { Component, OnInit } from '@angular/core';
import { ValuationHttpService } from '../../../../tzb/valuation/http/valuation.http.service';
import { ActivatedRoute, Params } from '@angular/router';
import { Message } from 'primeng/primeng';

@Component({
  selector: 'app-variety',
  templateUrl: './variety.component.html',
  styleUrls: ['./variety.component.scss'],
  providers: [ValuationHttpService]
})
export class VarietyComponent implements OnInit {
  title: string = '基础利率种类维护审核';
  public item: any = [];
  public data: any = {};
  public list: any = {};
  public scheduleId: string;
  public taskCategoryId: string;
  //提示信息
  public msgs: Message[] = [];
  constructor(public activatedRoute: ActivatedRoute,
    public httpService: ValuationHttpService) {

  }
  ngOnInit() {
    let param = {}
    if (this.activatedRoute.snapshot.params.data) {
      param = JSON.parse(this.activatedRoute.snapshot.params.data);
      this.scheduleId = this.activatedRoute.snapshot.params.scheduleId
      this.taskCategoryId = this.activatedRoute.snapshot.params.taskCategoryId
      this.list = JSON.parse(this.activatedRoute.snapshot.params.data);
    }
    let ob = this.httpService.queryRateTypeByIdService(param)
    ob.subscribe(data => {
      this.data = data.resultMap;
      this.item = data;
    })
  }
  reject(){}
 //确定审核
 confirm() {
  let param = {
    //  taskCategoryId: this.taskCategoryId,
    taskCategoryId: this.taskCategoryId,
    scheduleId: this.scheduleId,
    inputMap: this.list,
    checkStatus: '1',
    checkOpinionssion: this.data.checkOpinionssion,
  }
  this.httpService.saveRateTypeService(param).subscribe(data => {
    this.data = data.dataMap;
    if (data.returnCode.code == "success") {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
    }
  }, error => {
    this.msgs = [];
    this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });

  });
}

}

