import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
//页面
import { ValuationWorkflow } from './valuation.component';//计价工作流
import { ReCheckIntRatesComponent } from './re-check-int-rates/re-check-int-rates.component';//利率维度维护审核
import { IntValueComponent } from './int-value/int-value.component';//利率维度值维护审核
import { VarietyComponent } from './variety/variety.component';//利率种类
import { BaseIntComponent } from './base-int/base-int.component';//基础利率
import { PlanComponent } from './plan/plan.component';//利率计划
import { DifferComponent } from './differ/differ.component';//差异化
import { CycleComponent } from './cycle/cycle.component';//周期重定价
import { NegotiableComponent } from './negotiable/negotiable.component';//可协商
import { ImportComponent } from './import/import.component';//基础利率导入
import { ProductComponent } from './product/product.component';//产品执行利率
import { MarkingActComponent } from './marking-act/marking-act.component'
// import { CarryOutComponent } from './carry-out/carry-out.component';//利率计算
const routes: Routes = [
    {
        path: '',
        component: ValuationWorkflow,
        children: [
            { path: '', redirectTo: 're-check-int-rates' },
            {
                path: 're-check-int-rates', component: ReCheckIntRatesComponent,
            },
            {
                path: 'int-value', component: IntValueComponent,
            },
            {
                path: 'variety', component: VarietyComponent,
            },
            {
                path: 'base-int', component: BaseIntComponent,
            },
            {
                path: 'plan', component: PlanComponent,
            },
            {
                path: 'differ', component: DifferComponent,
            },
            {
                path: 'cycle', component: CycleComponent,
            },
            {
                path: 'negotiable', component: NegotiableComponent,
            },
            {
                path: 'product', component: ProductComponent,
            },
            {
                path: 'import', component: ImportComponent,
            },
            {
                path: 'marking-act', component: MarkingActComponent,
            },
        ]
    },
];

export const ValuationWorkflowRouting = RouterModule.forChild(routes);






