import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ActivatedRoute, Params, ParamMap } from '@angular/router';
import { ValuationHttpService } from '../../../../tzb/valuation/http/valuation.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
// import { IsUnit, IsLable2, IsOrigin,IsRcord, } from '././../../../valuation/constant/codeValue';
import { CommonHttpService } from '../../../common/http/common.http.service';
import * as _ from 'lodash';
import { BeanInterest } from '../../../valuation/interest-rate-manage/loan-interest-rate/interest-rate-allocation/base-interest/bean/base-interest.bean';
@Component({
  selector: 'app-import',
  templateUrl: './import.component.html',
  styleUrls: ['./import.component.scss'],
  providers: [ValuationHttpService, Commfunc]
})
export class ImportComponent implements OnInit {
  title: string = '贷款基础利率审核';
  list: any;
  msgs: any[] = [];
  public total: number;
  public beanInterest: BeanInterest = new BeanInterest();
  public isUnit: any = [];
  public isOrigin: any = [];
  public isLable2: any = [];
  public IsRcord: any = [];
  code: any;//获取所有码值
  public guarantee: any = []; //担保方式
  public loan: any = []; // 贷款金额
  public term: any = []; // 期限
  public product: any = [];// 积数比
  public mechanismDestile: any;
  public mechanism: any = [];
  public moenyTapy: any = []; //币种
  areas: any = [];
  //传入的值
  @Input() inValue: string;
  //出参
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  public first: number = 0;//分页控制
  public param: any;
  constructor(public activatedRoute: ActivatedRoute,
    public httpService: ValuationHttpService,
    public commfunc: Commfunc, public route: ActivatedRoute,
    public commonHttpService: CommonHttpService) {
    this.codeValues();
    this.isUnit = this.code['IsUnit'];
    this.isOrigin = this.code['IsOrigin'];
    this.isLable2 = this.code['IsLable2'];
    this.IsRcord = this.code['IsRcord'];
  }

  ngOnInit() {
    //币种
    let param6 = { dmsnlChiNm: "币种" };
    this.httpService.querydetailTmsDicService(param6).subscribe(data => {
      if (data.resultList) {
        this.moenyTapy = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue")
      }
    });

    //金额服务dmsnlValue:this.inValue['intClcnAmt'],
    let param1 = { dmsnlChiNm: "贷款金额" };
    this.httpService.querydetailTmsDicService(param1).subscribe(data => {
      if (data.resultList) {
        this.loan = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue")
      }
    });

    let param2 = { dmsnlChiNm: "担保方式" };
    this.httpService.querydetailTmsDicService(param2).subscribe(data => {
      if (data.resultList) {
        this.guarantee = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue")
      }
    });

    //期限
    let param3 = { dmsnlChiNm: "期限" };
    this.httpService.querydetailTmsDicService(param3).subscribe(data => {
      if (data.resultList) {
        this.term = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue");
      }
    });

    //积数比
    let param4 = { dmsnlChiNm: "积数比" };
    this.httpService.querydetailTmsDicService(param4).subscribe(data => {
      if (data.resultList) {
        this.product = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue")
      }
    });

    //机构片区
    // let param5 = { dmsnlChiNm: "机构片区" };
    // this.httpService.querydetailTmsDicService(param5).subscribe(data => {
    //   if (data.resultList) {
    //     this.mechanism = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue")
    //   }
    // });
    this.toquery();
    // let param5={
    //   orgId:this.list.institArea,
    //   relationshipType: "0"
    // }
    // this.commonHttpService.queryOrgByOrgIdAndRelationship(param5).subscribe(data => {
    //   this.mechanism = this.commfunc.toChangeTree(data.orgList, "orgName", "orgId")
    // })
    this.beanInterest.pageSize = 10;
    this.beanInterest.pageNum = 1;

  }
  toquery() {
    // let param = {
    //   intRateCode: this.inValue['intRateCode'],
    //   intRateCgy: this.inValue['intRateCgy'],
    //   effDt: this.inValue['effDt'],
    // }
    this.param = {
      intRateCgy: this.route.snapshot.params['intRateCgy'],
      effDt: this.route.snapshot.params['effDt'],
      operaFlag: this.route.snapshot.params['operaFlag'],
      intRateUnitTpCode: this.route.snapshot.params['intRateUnitTpCode'],
      pageSize: this.beanInterest.pageSize,
      pageNum: this.beanInterest.pageNum
    }

    this.param = {
      intRateCgy: this.route.snapshot.params['intRateCgy'],
      effDt: this.route.snapshot.params['effDt'],
      ccyCode: this.route.snapshot.params['ccyCode'],
      intRateMat: this.route.snapshot.params['intRateMat'],
      intRateUnitTpCode: this.route.snapshot.params['intRateUnitTpCode'],
      intRateVal: this.route.snapshot.params['intRateVal'],
      operaFlag: this.route.snapshot.params['operaFlag'],
      mechanismList: this.route.snapshot.params['mechanismList'],
      pageSize: this.beanInterest.pageSize,
      pageNum: this.beanInterest.pageNum
    }

    if (this.param.intRateMat) {
      this.httpService.queryLoansBaseRateSelectService(this.param).subscribe(data => {
        this.list = data.resultList;
        this.total = data;
        this.list.forEach((item, i) => {
          let param5 = {
            orgId: item.institArea,
            relationshipType: "0"
          }
          this.commonHttpService.queryOrgByOrgIdAndRelationship(param5).subscribe(data => {
            this.list[i]['institArea'] = data.orgList[0].orgName;
            this.list = _.clone(this.list);
          })
        });
      })
    } else {
      this.httpService.queryLoansBaseRateImportService(this.param).subscribe(data => {
        this.list = data.resultList;
        this.total = data.total;
        this.list.forEach((item, i) => {
          let param5 = {
            orgId: item.institArea,
            relationshipType: "0"
          }
          this.commonHttpService.queryOrgByOrgIdAndRelationship(param5).subscribe(data => {
            this.list[i]['institArea'] = data.orgList[0].orgName;
            this.list = _.clone(this.list);
          })
        });
      }, err => {
      })
    }

  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.beanInterest.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.beanInterest.pageNum = currentPage;
    this.first = (currentPage - 1) * this.beanInterest.pageSize;

    //调用查询的方法
    this.toquery();
    //  this.beanInterest.pageNum = 1;
  }
}
