import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { ValuationHttpService } from '../../../../tzb/valuation/http/valuation.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Message } from 'primeng/primeng';
import { CusOperationHttpService } from '../../../../tzb/custom/http/custom-operation/custom-operation.http.service'
import { DiffererentiationBean } from '../../../valuation/interest-rate-manage/loan-rule/differerentiation/bean/differerentiation.bean';
@Component({
  selector: 'app-marking-act',
  templateUrl: './marking-act.component.html',
  styleUrls: ['./marking-act.component.scss'],
  providers: [CusOperationHttpService, Commfunc]
})
export class MarkingActComponent implements OnInit {
  // public isprefFloatCode2: any = IsprefFloatCode2;
  // public IsRewardType: any = IsRewardType;
  title: string = '营销活动利率配置';
  list: any = [];
  productId: any;
  public aa: any;
  //传入的值
  @Input() inValue: string;
  //出参
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  //提示信息
  public msgs: Message[] = [];
  public differerentiationBean: DiffererentiationBean = new DiffererentiationBean();
  public first: number = 0;//分页控制
  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public activeRoute: ActivatedRoute, ) { }

  ngOnInit() {
    // this.aa = this.activeRoute.snapshot.params['data'];
    this.differerentiationBean.pageSize = 10;
    this.differerentiationBean.pageNum = 1;
    this.toquery();
  }
  toquery() {
    // let param={marketingCampaignId:this.activeRoute.snapshot.params['data']}
    let param = JSON.parse(this.activeRoute.snapshot.params['data'])
    this.httpService.queryCampaignPromotions(param).subscribe(data => {
      this.productId = data.productId;
      if (data.campaignTools) {
        this.list = data.campaignTools;
      }
      if (data.returnCode.code == "success") {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    });
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.differerentiationBean.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.differerentiationBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.differerentiationBean.pageSize;
    //调用查询的方法
    this.toquery();
  }
  //返回上一结点
  back() {
    window.history.go(-1)
  }
  reject(){}
}
